.menu-layout {
  display: flex;
  width: 100vw;
  height: 92vh;
  background-color: #f5f5f5;
  justify-content: center;
  align-items: center;
  animation: menu-load 1s;
}
@keyframes menu-load {
  from {
    transform: translateY(100vh);
  }
  to {
    transform: translateY(0vh);
  }
}
.menu-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  column-gap: 5vw;
  width: 90vw;
  height: 80vh;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 40px;
  border: 4px solid rgba(243, 6, 106, 0.6);
}
.menu-column {
  position: relative;
  width: 25vw;
  height: 75vh;
  display: flex;
  flex-direction: column;
}
.logo-box {
  position: relative;
  margin: 0vh 4vw;
}
.logo-box img {
  position: absolute;
  top: -2vh;
  width: 5vw;
  stroke-width: 5;
}
.logo {
  font-family: "Grand Hotel", cursive;
  font-size: 48px;
  background: linear-gradient(
    to right,
    #eb3677 0%,
    #eb3677 57%,
    #4b79ef 57%,
    #4b79ef 60%,
    #f3066a 60%,
    #f3066a 100%
  );
  background-clip: text;
  /*add this line, it defines how far the background should extend within an element, here we set it to text */
  -webkit-background-clip: text;
  /* add this line, for browsers compatibility */
  -webkit-text-fill-color: transparent;
}
.title {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-size: 64px;
  line-height: 96px;
  color: white;
  text-align: center;
  border-bottom: 1px solid white;
}

.meal-type {
  position: relative;
  font-family: "Grand Hotel";
  font-style: normal;
  font-weight: 400;
  font-size: 7vh;
  line-height: 87px;
  color: #eb3677;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin-bottom: 1vh;
}
.meal-list {
  position: relative;
  list-style-type: none;
  width: 100%;
  padding: 0;
  margin: 0;
}

.add-meal-btn {
  position: absolute;
  bottom: 0;
  width: 25vw;
  height: 6vh;
  align-items: center;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  background-color: #eb3677;
  color: white;
  margin: 0;
}
.breakfast-container {
  display: flex;
  flex-direction: column;
  height: 58vh;
  align-items: center;
  border-bottom: 1px solid white;
  overflow-y: scroll;
  overflow-x: hidden;
}
.breakfast-container::-webkit-scrollbar {
  display: none;
}
.dinner-container {
  display: flex;
  flex-direction: column;
  height: 63vh;
  align-items: center;
  overflow-y: scroll;
  overflow-x: hidden;
}
.dinner-container::-webkit-scrollbar {
  display: none;
}
.supper-container {
  display: flex;
  flex-direction: column;
  height: 32vh;
  align-items: center;
  overflow-y: scroll;
  overflow-x: hidden;
}
.supper-container::-webkit-scrollbar {
  display: none;
}
.extra-container {
  display: flex;
  flex-direction: column;
  height: 40vh;
  align-items: center;
  overflow-y: scroll;
  overflow-x: hidden;
}
.extra-container::-webkit-scrollbar {
  display: none;
}
@media (max-width: 1000px) {
  .menu-layout {
    height: auto;
  }
  .menu-container {
    height: auto;
    flex-direction: column;
    margin: 5vh 0;
  }
  .logo-box {
    margin-top: 3vh;
  }
  .menu-column {
    width: auto;
    align-items: center;
  }
  .add-meal-btn {
    padding: 0.4vh 3vw;
    width: 60vw;
  }
  .breakfast-container,
  .dinner-container,
  .supper-container,
  .extra-container {
    width: 70vw;
  }
  .logo-box img {
    display: none;
  }
}
